    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{background-color:#f6f7f8;font-size:100%;box-sizing:border-box}body{margin:0;padding:0;height:100vh;font-family:"Open Sans", Arial, Helvetica, sans-serif}*,*::before,*::after{box-sizing:inherit}.App{display:flex;flex-direction:column;text-align:center;min-height:100vh}.container{flex:1 1}.App-logo{height:40vmin;pointer-events:none}.App-header{background-color:#fafafa;border:black;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:black}.billboard{background-color:#303846;height:22rem;display:flex;align-items:center;justify-content:center}.billboard .billboard-text{color:white;margin:0 6vw}.billboard h1{text-align:center;color:white;margin:0 0 0.5rem 0;font-size:48px}.billboard h5{margin:0}.billboard .billboard-text__nickname{color:#10b9b3}.billboard .billboard-social{display:inline-flex}.billboard .billboard-social li a{display:inline-block}.billboard .billboard-social li a:hover{color:#10b9b3}.billboard .billboard-image{align-items:center;margin-top:23rem}.billboard .billboard-image img{border-radius:50%;-webkit-user-select:none;-ms-user-select:none;user-select:none;pointer-events:none}.container{height:100vh;margin:1rem;display:flex;flex-wrap:wrap}.container .content{width:70rem;margin:0 auto;text-align:left}.container .content>div:not(:first-of-type){margin-top:5rem}.container .content>div:last-of-type{margin-bottom:5rem}.container .content .content--about__paragraph{width:100%}.container .content .content--contact a{display:inline-block;font-size:large;color:#303846;text-decoration:underline}.container .content .content--contact a:hover{color:#10b9b3}.container h1{text-align:left;width:8rem;padding:0.5rem 0 0}.container .downloadCV--button{background-color:#303846;color:#fff;border:1px solid #303846;padding:8px;width:10rem;border-radius:16px;text-align:center}.container .downloadCV--button:hover{background:#10b9b3;border:1px solid #10b9b3}.content--projects__soshealthcare:hover{opacity:62%}@media only screen and (max-width: 768px){.billboard-text>h1{font-size:32px}.fa-2x{font-size:1.5em}}@media only screen and (max-width: 1440px){.billboard-image img{display:none}}@media only screen and (min-width: 1440px){.container .content .content--about__paragraph{width:40rem;line-height:1.5}}

ul{list-style-type:none;margin:0;padding:0}li{display:inline}a{display:block;text-decoration:none}ul li>a{padding:8px;color:#fff}nav.navbar{background-color:#303846;display:flex;justify-content:space-between;align-items:center;overflow:hidden;position:sticky;top:0;width:100%;height:50px;padding:0 1rem}nav.navbar .navbar-name{font-size:24px;font-weight:700;letter-spacing:3px;color:#fff}nav.navbar .navbar-name:hover{color:#10b9b3}nav.navbar .navbar-name__nickname{color:#10b9b3}nav.navbar ul{display:flex;align-items:center;justify-content:flex-end}nav.navbar ul li{margin-right:20px}nav.navbar ul li:last-child{margin-right:0px}nav.navbar ul li a:hover,nav.navbar ul li a:active{color:#10b9b3}nav.navbar ul li a{font-size:14px;font-weight:bold}@media only screen and (max-width: 320px){nav.navbar .navbar-name{font-size:24px}}@media only screen and (max-width: 600px){nav.navbar{flex-wrap:wrap;height:110px;padding:1rem;grid-row-gap:16px;row-gap:16px}nav.navbar .navbar-name{font-size:24px;letter-spacing:2px}nav.navbar ul{display:flex;flex-basis:100%;align-items:center;justify-content:space-around}}@media only screen and (min-width: 768px){nav.navbar{display:flex;flex-wrap:wrap;justify-content:space-around;align-items:center}}


